<template>
  <div class="list-container">
    <h2>
      <i class="iconfont ic" @click="toSort">&#xe637;</i>
      全部男子鞋
    </h2>
    <div class="result">
      <span>970个结果</span>
      <span @click="show=true">
        <i class="iconfont ic-two">&#xe634;</i>
      </span>
      <van-action-sheet v-model="show">
        <div class="select-B">
          <p>筛选条件</p>
          <p>
            <span @click="show=false">排序方式</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span>尺码</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span>品牌</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span>筛选色</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span>产品系列</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p class="lastOne" @click="show=false">
            <span>产品类型</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <div
            class="btn"
            @click="show=false"
            @touchstart="touchstart()"
            @touchend="touchend()"
            :class="{touchColor:whether}"
          >查看产品</div>
        </div>
      </van-action-sheet>
    </div>

    <!-- <van-action-sheet v-model="show" :actions="actions" @select="onSelect" /> -->
    <!-- 
            1.添加mt-loadmore组件
            2.添加loadTop、loadBottom、allLoaded和ref
            3.将默认的auto-fill改为false
    -->
    <mt-loadmore
      :top-method="loadTop"
      :bottom-method="loadBottom"
      :bottom-all-loaded="allLoaded"
      ref="loadmore"
      :auto-fill="false"
    >
      <!-- 使用v-for渲染商品列表 -->
      <ul class="list">
        <!-- 因为mongodb里面的id有_  所以是_id -->
        <li
          v-for="(item,index) in list"
          :key="index"
          class="list-content"
          @click="toDetail(item._id)"
        >
          <div class="pic">
            <img :src="$commonUrl+'/static'+item.url" />
          </div>
          <div class="pic-introduction">
            <p class="frist">{{item.name}}</p>
            <p class="second">{{item.type}}</p>
            <p class="third">{{'¥'+item.price}}</p>
          </div>
        </li>
      </ul>
    </mt-loadmore>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 是否全部加载完毕
      allLoaded: false,

      // 列表
      list: [],
      // 页数
      page: 1,
      show: false,
      whether: false
    };
  },
  methods: {
    toSort() {
      this.$router.push("/sort");
    },
    touchstart() {
      this.whether = true;
    },
    touchend() {
      this.whether = false;
    },
    // 从上往下拉,刷新页面
    loadTop() {
      //   console.log(123);
      // 获取第一页
      this.$axios
        .post("/goods/list", {
          pageNum: 1
        })
        .then(res => {
          const { code, msg, list } = res.data;

          // 整个列表显示第一页
          this.list = list;
          // 页数为1
          this.page = 1;

          // 去掉正在加载的顶部loading
          this.$refs.loadmore.onTopLoaded();
          //开启加载更多   意思就是如果加载到第三页后,点刷新页面,回到第一页后,又可以继续加载后面的页数
          this.allLoaded = false;
        });
    },
    // 从底部往上滑,加载更多
    loadBottom() {
      // 尝试让页数+1
      const newPage = this.page + 1; //相当于第二页

      // 尝试获取下一页
      this.$axios
        .post("/goods/list", {
          pageNum: newPage
        })
        .then(res => {
          const { code, msg, list } = res.data;

          // 如果下一页有数据
          if (list.length) {
            // 将数据拼接到旧数据的后面
            this.list = this.list.concat(list); //第一页和之后的页面数据相拼起来

            // 更改页数
            this.page = newPage;
          } else {
            // 关闭上拉加载更多的功能
            this.allLoaded = true;
          }

          // 关闭正在加载的底部提示
          this.$refs.loadmore.onBottomLoaded();
        });
    },
    toDetail(id) {
      //  通过id跳到详情
      // console.log(id)
      this.$router.push("/goodsdetail/" + id);
    }
  },
  //页面初始化的时候,手动加载第一页,相当于刷新一次列表
  mounted() {
    this.loadTop();
  }
};
</script>

<style lang="scss" scoped>
.list-container {
  height: 100vh;
  overflow: scroll;

  h2 {
    position: relative;
    text-align: center;
    font-size: 0.23rem;
    line-height: 0.8rem;
    color: #010101;
    letter-spacing: 1.5px;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 999;
    .ic {
      position: absolute;
      top: 0;
      left: 0.3rem;
    }
  }
  .result {
    padding: 0 0.2rem;
    line-height: 0.86rem;
    border-top: 1px solid #b5b5b5;
    font-size: 0.24rem;
    letter-spacing: 2px;
    position: relative;

    .ic-two {
      color: #000;
      position: absolute;
      right: 0.1rem;
      top: 0;
      font-size: 0.3rem;
    }
  }
}

.select-B {
  padding: 0.3rem 0.3rem 0;

  p {
    overflow: hidden;
  }
  p:nth-child(1) {
    padding-left: 0.4rem;
    font-weight: bolder;
    font-size: 0.4rem;
  }
  p:not(:first-child) {
    // 除第一个以外的元素
    font-size: 0.24rem;
    color: #b2b2b2;
    border-bottom: 0.333333px solid #c8c8c8;
  }

  .lastOne {
    border: 0 !important;
  }
  span:nth-child(1) {
    float: left;
  }
  span:nth-child(2) {
    float: right;
    color: #5a7883;
  }
  .btn {
    margin-top: 0.2rem;
    text-align: center;
    background-color: #000;
    color: aliceblue;
    font-size: 0.24rem;
  }

  .touchColor {
    color: #fff;
    background-color: #7f7f7f;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .list-content {
    width: 49%;
    font-size: 0.4rem;

    // &:nth-child(odd) .fisrt {
    //   color: red; // 如果要写其中一个li
    // }

    img {
      width: 100%;
      height: 3.8rem;
      object-fit: cover;
    }

    .pic-introduction {
      padding: 0 10px;
      z-index: 123;

      .frist {
        padding-top: 0.11rem;
        padding-bottom: 0.26rem;
        font-size: 0.24rem;
        color: #000;
      }
      .second {
        font-size: 0.23rem;
        color: #5b5b5b;
        line-height: 0.46rem;
      }
      .third {
        font-size: 0.23rem;
        color: #000;
        letter-spacing: 0.8px;
        padding-bottom: 0.3rem;
        font-family: cursive;
      }
    }
  }
}
</style>