<template>
  <div id="app">
    <!-- 导航 -->
    <div id="nav" v-if="$route.meta.showNav">
      <router-link to="/">
        <!-- <span class="logo">木</span> -->
        <i class="iconfont">&#xe60b;</i>
      </router-link>
      <router-link to="/sort">
        <i class="iconfont">&#xe619;</i>
      </router-link>
      <router-link to="/ShopCar">
        <i class="iconfont">&#xe640;</i>
      </router-link>
      <router-link to="/userpersonal">
        <i class="iconfont">&#xe609;</i>
      </router-link>
    </div>
    <!-- 路由入口 -->
    <router-view />
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    // 获取用户信息
    getUserInfo() {
      return this.$axios.post("/user2/info").then(res => {
        const { code, msg, userInfo } = res.data;

        if (code == 1) {
          // 调用store对象的方法,改变userInfo的值
          this.$store.commit("userInfo", userInfo);
        } else {
          alert(msg);
        }
      });
    }
  },
  created() {
    // console.log("根组件");
    // 打开页面,首先执行根组件这里的代码,之后,无论怎么跳转,这里的代码都不会再走

    // 获取token
    const token = localStorage.getItem("token");

    // 如果token存在
    if (token) {
      // 存储token,放到store
      // 1.直接使用commit一个mutation函数
      this.$store.commit("token", token);
      // 2.使用dispatch一个action函数
      // this.$store.dispatch('token',token)

      // 获取用户信息
      this.getUserInfo();
    }
  }
};
</script>

