<template>
  <div class="detail-container" v-if="info">
    <h2>
      <i class="iconfont ic" @click="backList">&#xe637;</i>
    </h2>
    <div class="pic">
      <img :src="$commonUrl+'/static'+ info.url" alt />
    </div>
    <div class="pic-content">
      <p>{{info.name}}</p>
      <p>{{info.type}}</p>
    </div>
    <div class="pic-content-two">
      <p>{{info.pcolor}}</p>
      <p>
        产品货号:
        <span>{{info.goods}}</span>
      </p>
      <p>
        <i class="iconfont ico">&#xe621;</i>
      </p>
    </div>
    <div class="product-introduce">
      <van-tabs v-model="active" :color="'#000'" :line-width="'200px'" :title-active-color="'#000'">
        <van-tab title="产品详情">
          <div class="tab-content">
            <p>80年代中期常见于球场，如今以新形象再现街头。这款经典鞋，采用单色皮革鞋面，质感舒适。缀缝制三条纹和三叶草徽标，纯正adidas Originals型格。Home of Classics系列 无对比色，只用三种不同暗调的白色乙烯/乙酸乙烯酯（EVA）中底含乙烯/乙酸乙烯酯（EVA）中底，旨在包裹足部</p>
            <ul class="specific">
              <li>常规型剪裁</li>
              <li>系带设计</li>
              <li>皮革鞋面</li>
              <li>橡胶全包打底</li>
            </ul>
          </div>
        </van-tab>
        <van-tab title="评论">
          <div class="tab-content">
            <p>80年代中期常见于球场，如今以新形象再现街头。这款经典鞋，采用单色皮革鞋面，质感舒适。缀缝制三条纹和三叶草徽标，纯正adidas Originals型格。Home of Classics系列 无对比色，只用三种不同暗调的白色乙烯/乙酸乙烯酯（EVA）中底含乙烯/乙酸乙烯酯（EVA）中底，旨在包裹足部</p>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <!-- <div class="buy" @click="addCar"> -->
    <div class="buy" @click="show=true">
      <p>
        购买
        <span>¥{{info.price}}</span>
        <span>
          <i class="iconfont">&#xe60a;</i>
        </span>
      </p>
    </div>
    <van-action-sheet v-model="show" title="选择商品规格">
      <div class="select">
        <!-- <p>选择商品规格</p> -->
        <div class="norm">
          <p>num</p>
          <p>
            <span @click="decrease">-</span>
            <span>{{num}}</span>
            <span @click="increase">+</span>
          </p>
        </div>
        <p class="size">size</p>
        <ul class="list">
          <li
            v-for="(item,index) in list"
            :key="index"
            class="list-content"
            @click="selectSize(item,$event)"
          >{{item.size}}</li>
        </ul>
        <div class="hide" v-show="isShow">
          <div class="btn2" @click=" addCar">
            <p>
              加入购物袋 ¥{{info.price}}
              
              <span>→</span>
            </p>
          </div>
          <div class="btn">
            <p @click="pay">
              去往购物车
              <span>→</span>
            </p>
          </div>
        </div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      active: 0,
      id: "",
      info: null,
      num: 1, // 必须默认从1开始加
      show: false,
      show2: false,
      isShow: false,
      list: [
        { size: 39 },
        { size: 40 },
        { size: 41 },
        { size: 42 },
        { size: 43 },
        { size: 44 },
        { size: 45 },
        { size: 46 }
      ],
      select: ""
    };
  },
  computed:
    // 如果有多个计算属性,添加mapState的时候,需要使用扩展运算符
    mapState(["token"]),

  methods: {
    backList() {
      this.$router.push("/goodslist");
    },
    selectSize(item, $event) {
      this.select = item.size;
      let li_dom = document.getElementsByClassName("list-content");
      for (let i = 0; i < li_dom.length; i++) {
        //选中后,其他li的颜色
        li_dom[i].style.color = "#000";
      }
      // 选中颜色
      event.target.style.color = " #e50707";

      this.isShow = true;
    },
    reqGoods() {
      // console.log(123);
      // 请求详情数据
      const id = this.id;
      this.$axios
        .post("/goods/detail", {
          id
        })
        .then(res => {
          const { code, msg, info } = res.data;

          this.info = info;
        });
    },
    increase() {
      this.num++;
    },
    decrease() {
      if (this.num > 1) {
        this.num--;
      } else {
      }
    },

    addCar() {
      // dispatch：含有异步操作，例如向后台提交数据，写法： this.$store.dispatch('action方法名',值)
      this.$store
        .dispatch("myAction", {
          goodsId: this.id,
          goodsNum: this.num,
          goodsName: this.info.name,
          goodsPrice: this.info.price,
          goodsImg: this.info.url
        })
        .then(res => {
          const { code, msg } = res.data;
          if (code == 1) {
            // 如果登录了就加入购物车
            console.log(msg);
            this.$router.push("/shopcar");
          } else {
            // 如果未登录就跳转登录
            // alert(msg);
            this.$router.push("/login");
          }
        });
    },
    pay(){
       this.$router.push('/shopcar')
    }
  },
  mounted() {
    // console.log("666");

    // 获取路由信息对象中的params,这是当前路由的参数集合,再取里面的id
    this.id = this.$route.params.id;

    // 请求获取商品信息
    this.reqGoods();
  }
};
</script>

<style lang="scss" scoped>
.detail-container {
  background-color: #ebecee;
  overflow: scroll;

  h2 {
    height: 0.9rem;
  }
