    font-size: 0.54rem;
    font-weight: lighter;
    color: #000;
  }

  .pic {
    width: 100%;
    text-align: center;
    img {
      width: 5.4rem;
      height: 4.5rem;
    }
  }

  .pic-content {
    margin-top: 0.4rem;
    padding: 0 0.6rem;
    p:nth-child(1) {
      font-size: 0.5rem;
      font-weight: bolder;
      color: #000;
    }
    p:nth-child(2) {
      font-size: 0.28rem;
      line-height: 0.9rem;
      color: #000;
    }
  }
  .pic-content-two {
    padding: 0.6rem 0.6rem;
    p {
      font-size: 0.28rem;
      line-height: 0.4rem;
      color: #000;
      span {
        padding-left: 0.2rem;
        font-size: 0.26rem;
        letter-spacing: 0.6px;
      }

      .ico {
        font-size: 0.6rem;
        font-weight: lighter;
      }
    }
    p:nth-child(3) {
      padding-top: 0.6rem;
    }
  }
  .product-introduce {
    border-top: 0.333333px solid #c8c8c8;
    background-color: #fff;
    margin-bottom: 1.6rem;
  }

  /deep/.van-tab {
    font-size: 0.24rem;
    letter-spacing: 2px;
    border-bottom: 0.3333px solid #b5b5b5b5;
  }

  .tab-content {
    padding: 0.4rem 0.6rem;
    font-size: 0.24rem;
    p {
      font-weight: thin;
      line-height: 0.38rem;
      letter-spacing: 0.4px;
      color: #000;
      //  两端对齐
      text-align: justify;
    }
  }

  .specific {
    padding: 0.3rem 0.6rem;
    li {
      list-style-type: disc;
      color: #000;
    }
  }

  .buy {
    z-index: 999;
    position: fixed;
    left: 0.2rem;
    bottom: 0.8rem;
    width: 7.1rem;
    height: 0.7rem;
    color: #fff;
    line-height: 0.7rem;
    background: #000;

    p {
      padding: 0 0.3rem;
      letter-spacing: 3px;
      font-weight: bold;
      font-size: 0.24rem;
      span:nth-child(1) {
        padding-left: 0.1rem;
      }
      span:nth-child(2) {
        float: right;
        i {
          font-size: 0.4rem;
          font-weight: lighter;
        }
      }
    }
  }
  .select {
    box-sizing: border-box;
    p {
      margin-top: 0.3rem;
      padding-left: 0.4rem;
      font-weight: bolder;
      font-size: 0.4rem;
    }
    .norm {
      padding: 0.3rem;
      display: flex;
      justify-content: space-between;
      p {
        span {
          display: inline-block;
          text-align: center;
          width: 0.6rem;
        }
        span:nth-child(1),
        span:nth-child(3) {
          border-radius: 2px;
          border: 1px solid #dcd6d6;
        }
        span:nth-child(2) {
          width: 0.8rem;
          border: 1px solid #eee;
        }
      }
    }
    .size {
      padding-left: 0.7rem;
    }
    .list {
      padding: 0.3rem 0 0.3rem 0.7rem;
      display: flex;
      flex-wrap: wrap;
      font-size: 0.36rem;
      .list-content {
        padding: 0.2rem 0;
        width: 20%;
      }
    }
    .btn2{
      
      margin-top: 0.2rem;
       letter-spacing: 2px;
      font-size: 0.24rem;
         line-height: 0.86rem;
      p {
        border: 1px solid #645e5e;
        margin: 0 0.3rem;
        font-size: 0.24rem;
        color: #000;
        span{
          padding-right: .3rem;
          font-size: .6rem;
          float: right;
          line-height: .7rem;
        }
      }
    }
    .btn {
      padding-bottom: .1rem;
      margin-top: 0.2rem;
      font-size: 0.24rem;
      line-height: 0.86rem;
      letter-spacing: 2px;
      p {
        margin: 0 0.3rem;
        background-color: #000;
        color: aliceblue;
        font-size: 0.24rem;
         span{
            padding-right: .3rem;
          font-size: .6rem;
              line-height: .7rem;
          float: right;
        }
      }
    }
  }

  /deep/.van-action-sheet__header {
    font-size: 0.4rem;
