<template>
  <div class="ic-back">
    <i class="iconfont" @click="toHome">&#xe637;</i>
    <h1>木</h1>
    <div class="login">
      <p class="content">
        <label for="username">用户名:</label>
        <input type="text" class="username" v-model="username" placeholder="请输入用户名" />
        <br />
        <span class="usernameSpan"></span>
      </p>
      <p class="content-copy">
        <label for="password">密&nbsp;&nbsp;&nbsp;码:</label>
        <input type="password" class="userpwd" v-model="userpwd" placeholder="请输入6-12位密码" />
        <br />
        <span class="userpwdSpan"></span>
      </p>
      <p class="tip">{{tip}}</p>
      <p class="sublogin" @click="submitLogin">登录</p>
      <br />
      <p class="forget" @click="toRegister">去注册</p>
    </div>
    <div class="policy">
      <p>首次登录将自动保存</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      userpwd: "",
      tip: ""
    };
  },
  methods: {
    toHome() {
      this.$router.push("/home");
    },
    toRegister() {
      this.$router.push("/Register");
    },
    submitLogin() {
      // console.log(123);
      this.$axios
        .post("/user/login", {
          username: this.username,
          password: this.userpwd
        })
        .then(res => {
          let { code, msg, token } = res.data;
          if (code == 1) {
            // 登录成功，先把token存在本地
            localStorage.setItem("token", token);

            // 登录时,往仓库中存token,commit提交
            this.$store.commit("token", token);

            // 获取用户信息,并且将其添加到store里面
            this.$parent.getUserInfo(token).then(() => {
              // 跳转到个人中心页面
              this.$router.push({ name: "home" });
            });

          } else if (code == -1) {
            this.tip = msg;
          } else if (code == -2) {
            this.tip = msg;
          } else {
            console.log(msg);
          }
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.ic-back {
  padding: 0.3rem;
  .iconfont {
    font-size: 0.4rem;
    color: #eae9ef;
  }

  h1 {
    padding-left: 0.3rem;
    font-size: 1.2rem;
    font-weight: 200;
    color: #000;
  }

  .login {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    width: 340px;
    padding: 10px 50px 50px 50px;
    height: 450px;
    text-align: left;
    label {
      font-size: 0.3rem;
      color: #000;
    }

    .content {
      margin-top: 2rem;
      margin-bottom: 0.4rem;
    }

    .content-copy {
      margin-top: 1rem;
    }

    .username,
    .userpwd {
      width: 4rem;
      border: 0;
      height: 0.4rem;
      line-height: 0.4rem;
      padding-left: 0.2rem;
      font-size: 0.4rem;
      font-weight: bold;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }

    input::-webkit-input-placeholder {
      color: #aab2bd;
      font-size: 0.2rem;
      letter-spacing: 2px;
      font-weight: normal;
    }

    .tip {
      width: 5.7rem;
      text-align: center;
      color: #ff0074;
      height: 0.68rem;
      line-height: 0.6rem;
    }

    .sublogin {
      display: inline-block;
      width: 5.6rem;
      line-height: 0.85rem;
      text-align: center;
      border-radius: 2px;
      font-size: 0.26rem;
      height: 0.85rem;
      background-color: #000;
      color: #fff;
      font-weight: bold;
    }

    .forget {
      width: 5.6rem;
      text-align: right;
      font-size: 0.23rem;
      line-height: 0.7rem;
      color: #bcbbc3;
    }
  }
  .policy {
    width: 100%;
    position: absolute;
    bottom: 0.2rem;
    left: 0;
    font-size: 0.24rem;
    p {
      text-align: center;
      color: #b2b2b9;
      font-weight: 300;
      z-index: 111;
      span {
        color: #000;
        font-weight: 400;
      }
    }
  }
}
</style>