<template>
  <div class="set">
    <h2>
      <i class="iconfont ic" @click="toPersonal">&#xe637;</i>设置
    </h2>
    <ul class="repeat-one">
      <li v-for="(item,index) in list" :key="index" class="repeat-content">
        {{item.name}}
        <span>
          <i class="iconfont">&#xe64c;</i>
        </span>
      </li>
    </ul>
    <ul class="repeat-two">
      <li v-for="(item,index) in list2" :key="index" class="repeat-content">
        {{item.name}}
        <span>
          {{item.num}}
          <i class="iconfont">&#xe64c;</i>
        </span>
      </li>
    </ul>
    <ul class="repeat-three">
      <li v-for="(item,index) in list3" :key="index" class="repeat-content">
        {{item.name}}
        <span>
          <i class="iconfont">&#xe64c;</i>
        </span>
      </li>
    </ul>
    <ul class="repeat-four">
      <li v-for="(item,index) in list4" :key="index" class="repeat-content">
        {{item.name}}
        <span>
          <i class="iconfont">&#xe64c;</i>
        </span>
      </li>
    </ul>
    <div class="btn" @click="quit">退出登录</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [{ name: "地址管理" }, { name: "收款账户" }],
      list2: [
        { name: "通知设置" },
        { name: "黑名单设置" },
        { name: "清除缓存", num: "0.00M" }
      ],
      list3: [{ name: "账号安全" }, { name: "官方认证" }],
      list4: [{ name: "反馈与建议" }, { name: "关于木APP" }, { name: "更新" }]
    }
  },methods: {
    toPersonal(){
      this.$router.push('/userpersonal')
    },
    quit(){
     
     // 清除token
       localStorage.removeItem('token')

      //  到首页
       this.$router.push('/home')
         //  页面重新加载 不然登录会必须要刷新了才能消失
       location.reload()
    }
  },
};
</script>

<style lang="scss" scoped>
.set {
  font-weight: 400;
  font-size: 0.3rem;
  height: 100vh;
  background-color: #f5f5f7;
  color: #000;
  h2 {
    background-color: #fff;
    font-size: 0.34rem;
    padding: 0.3rem 0;
    text-align: center;
    font-weight: 560;
    .ic {
      font-size: 0.5rem;
      position: absolute;
      top: 0.3rem;
      left: 0.3rem;
    }
  }
  .repeat-content {
    background-color: #fff;

    padding: 0 0.6rem;
    line-height: 0.9rem;
    border-bottom: 0.3333px solid #f5f5f7;

    span {
      font-weight: lighter;
      font-size: 0.28rem;
      color: #3e3e3e;
      position: absolute;
      right: 0.3rem;
      i {
        // 属性设置元素的垂直对齐方式
        vertical-align: middle; //  把此元素放置在父元素的中部
      }
    }
  }

  .repeat-one,
  .repeat-two,
  .repeat-three,
  .repeat-four {
    border-bottom: 10px solid #f5f5f7;
  }

  .btn {
    background-color: #fff;
    line-height: 0.9rem;
    text-align: center;
  }
}
</style>