import Vue from 'vue'
import Router from 'vue-router'

//使用路由插件
Vue.use(Router)

import Home from './views/Home.vue'
import Register from './views/Register.vue'
import Login from './views/Login.vue'

import Sort from './views/sort.vue'
import GoodsList from './views/GoodsList.vue'

import GoodsList2 from './views/GoodsList2.vue'
import GoodsDetail from './views/GoodsDetail.vue'

import UserPersonal from './views/UserPersonal.vue'
import  quitLogin  from './views/quitLogin.vue'

import ShopCar from './views/ShopCar.vue'

import GoodsOrder from './views/GoodsOrder'

//  实例化路由对象
const router = new Router({
  // mode: 'history',
  mode: 'hash',

  // 根路径
  base: process.env.BASE_URL,

  // 各种路径
  routes: [
    
    // 利用重定向
    {
      path: '/',
      redirect: '/home'
    },

    // 首页
     {
      path: '/home',
      name: 'home',
      component: Home,
      meta:{
        showNav:true,// 显示
      }
    },

    // 注册
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta:{
        showNav:false,// 隐藏
      }
    },

    // 登录
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta:{
        showNav:false,
      }
    },

    //分类
    {
      path: '/sort',
      name: 'sort',
      component: Sort,
      meta:{
        showNav:true,
      }
    },

    // 列表
    {
      path: '/goodslist',
      name: 'goodslist',
      component: GoodsList,
      meta:{
        showNav:true,
      }
    },
    {
      path: '/goodslist2',
      name: 'goodslist2',
      component: GoodsList2,
      meta:{
        showNav:true,
      }
    },

    // 详情
    {
      path: '/goodsdetail/:id',
      name: 'goodsdetail',
      component: GoodsDetail,
      meta:{
        showNav:true,
      }
    },
 

    // 个人中心
    {
      path: '/userpersonal',
      name: 'userpersonal',
      component:UserPersonal,
      meta:{
        showNav:true,
      }
    },

    // 设置
   
    {
      path: '/quitlogin',
      name: ' quitlogin',
      component: quitLogin,
      meta:{
        showNav:false,
      }
    },

    //  购物车列表
    {
      path: '/shopcar',
      name: ' shopcar',
      component: ShopCar,
      meta:{
        showNav:true,
      }
    },

    {
      path: '/goodsorder',
      name: ' goodsorder',
      component: GoodsOrder,
      meta:{
        showNav:false,
      }
    },

    //  订单

    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // }
  ]
})

//导出
export default router
