<template>
  <div class="list-container">
    <h2>
      <i class="iconfont ic" @click="toSort">&#xe637;</i>
      全部男子鞋
    </h2>
    <div class="result">
      <span>970个结果</span>
      <span @click="show=true">
        <i class="iconfont ic-two">&#xe634;</i>
      </span>
      <van-action-sheet v-model="show">
        <div class="select-B">
          <p>筛选条件</p>
          <p>
            <span @click="show=false">排序方式</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span>尺码</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span>品牌</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span>筛选色</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span>产品系列</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p class="lastOne" @click="show=false">
            <span>产品类型</span>
            <span>
              全部
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <div
            class="btn"
            @click="show=false"
            @touchstart="touchstart()"
            @touchend="touchend()"
            :class="{touchColor:whether}"
          >查看产品</div>
        </div>
      </van-action-sheet>
    </div>

    <!-- <van-action-sheet v-model="show" :actions="actions" @select="onSelect" /> -->
    <!-- 
            1.添加mt-loadmore组件
            2.添加loadTop、loadBottom、allLoaded和ref
            3.将默认的auto-fill改为false
    -->
    <!-- 使用v-for渲染商品列表 -->
    <ul class="list">
      <!-- 因为mongodb里面的id有_  所以是_id -->
      <li
        v-for="(item,index) in list"
        :key="index"
        class="list-content"
        @click="toDetail(item._id)"
      >
        <div class="pic">
          <img :src="$commonUrl+'/static'+item.url" />
        </div>
        <div class="pic-introduction">
          <p class="frist">{{item.name}}</p>
          <p class="second">{{item.type}}</p>
          <p class="third">{{'¥'+item.price}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //$router : 是路由操作对象，只写对象
      //$route : 路由信息对象，只读对象
      value: this.$route.params.value,
      // 列表
      list: [],
      show: false,
      whether: false
    };
  },
  // 生命周期函数，页面一加载就渲染
  created() {
    this.$axios
      .post("/goods/fuzzy", {
        keyword: this.value
      })
      .then(res => {
          this.list = res.data.list
        // const { code, list } = res.data;
        console.log(res.data.list);
      });
  },
  methods: {
    toSort(){
      this.$router.push('/sort')
    },
    touchstart() {
      this.whether = true;
    },
    touchend() {
      this.whether = false;
    },
    toDetail(id) {
    //  通过id跳到详情
    this.$router.push("/goodsdetail/" + id);
  }
  }
  
};
</script>

<style lang="scss" scoped>
.list-container {
  height: 100vh;
  overflow: scroll;

  h2 {
    position: relative;
    text-align: center;
    font-size: 0.23rem;
    line-height: 0.8rem;
    color: #010101;
    letter-spacing: 1.5px;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 999;
    .ic {
      position: absolute;
      top: 0;
      left: 0.3rem;
    }
  }
  .result {
    padding: 0 0.2rem;
    line-height: 0.86rem;
    border-top: 1px solid #b5b5b5;
    font-size: 0.24rem;
    letter-spacing: 2px;
    position: relative;

    .ic-two {
      color: #000;
      position: absolute;
      right: 0.1rem;
      top: 0;
      font-size: 0.3rem;
    }
  }
}

.select-B {
  padding: 0.3rem 0.3rem 0;

  p {
    overflow: hidden;
  }
  p:nth-child(1) {
    padding-left: 0.4rem;
    font-weight: bolder;
    font-size: 0.4rem;
  }
  p:not(:first-child) {
    // 除第一个以外的元素
    font-size: 0.24rem;
    color: #b2b2b2;
    border-bottom: 0.333333px solid #c8c8c8;
  }

  .lastOne {
    border: 0 !important;
  }
  span:nth-child(1) {
    float: left;
  }
  span:nth-child(2) {
    float: right;
    color: #5a7883;
  }
  .btn {
    margin-top: 0.2rem;
    text-align: center;
    background-color: #000;
    color: aliceblue;
    font-size: 0.24rem;
  }

  .touchColor {
    color: #fff;
    background-color: #7f7f7f;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .list-content {
    width: 49%;
    font-size: 0.4rem;

    // &:nth-child(odd) .fisrt {
    //   color: red; // 如果要写其中一个li
    // }

    img {
      width: 100%;
      height: 3.8rem;
      object-fit: cover;
    }

    .pic-introduction {
      padding: 0 10px;
      z-index: 123;

      .frist {
        padding-top: 0.11rem;
        padding-bottom: 0.26rem;
        font-size: 0.24rem;
        color: #000;
      }
      .second {
        font-size: 0.23rem;
        color: #5b5b5b;
        line-height: 0.46rem;
      }
      .third {
        font-size: 0.23rem;
        color: #000;
        letter-spacing: 0.8px;
        padding-bottom: 0.3rem;
        font-family: cursive;
      }
    }
  }
}
</style>