<template>
  <div class="shopcar">
    <div v-if="!list.length" class="car">
      <div class="content">
        <h2>你的购物袋为空</h2>
        <p>没有挑到心仪商品?让我们为你创造更多灵感吧!</p>
        <div
          class="btn"
          @touchstart="touchstart()"
          @touchend="touchend()"
          @click="toList"
          :class="{touchColor:whether}"
        >
          探索潮流趋势
          <p>→</p>
        </div>
      </div>
    </div>
    <div v-else class="mycar">
      <div class="car-head">
        <div class="head-left">
          <!-- <i class="iconfont icon-xuanzhong3 ic"></i> -->
          <i
            class="iconfont ic2"
            :class="[isAll? 'icon-xuanzhong3':'icon-weixuanzhong11']"
            @click="chooseAll"
          ></i>
          <span>全选</span>
        </div>
        <div class="head-right">
          <span>￥{{goodsTotal}} 合计</span>
          <i class="iconfont ic-copy" @click="delAll">&#xe61d;</i>
        </div>
      </div>
      <div class="car-content">
        <p class="title">选择要结账的商品</p>
        <!-- 循环开始 -->
        <template v-for="(item,index) in list">
          <div class="carlist" :key="index">
            <div class="choose-one">
              <!-- 动态class -->
              <i
                class="iconfont icon"
                @click="chooseItem(item,index)"
                :class="[item.chosen ? 'icon-xuanzhong3' : 'icon-weixuanzhong11']"
              ></i>
            </div>
            <!-- <div class="choose-one"><i class="iconfont  icon-xuanzhong3 icon"></i></div>  -->
            <div class="list-content">
              <div class="pic">
                <img :src="$commonUrl+'/static'+item.goodsImg" />
              </div>
              <div class="info">
                <p>{{item.goodsName}}</p>
                <p>{{'¥'+item.goodsPrice}}</p>
                <div class="num">
                  <!-- 因为要涉及数组变异,所以要传下标和元素 -->
                  <!-- :disabled="item.goodsNum == 1" -->
                  <span @click="decrease(item,index)">-</span>
                  <span>{{item.goodsNum}}</span>
                  <span @click="increase(item,index)">+</span>
                  <span @click="delItem(item,index)">删除</span>
                </div>
              </div>
            </div>
          </div>
        </template>
        <!-- 结束 -->
      </div>
      <div class="pay-border">
        <div class="pay">
          <p
            class="btn"
            @touchstart="touchstart()"
            @touchend="touchend()"
            @click="toOrder"
            :class="{touchColor:whether}"
          >
            结算
            <span>
              <i class="row">→</i>
            </span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 引入vuex的map辅助函数
import { mapState } from "vuex";

import { Toast } from "vant";

export default {
  data() {
    return {
      whether: false,
      list: [],
      goodsTotal: 0
    };
  },
  computed: {
    // 全选
    isAll: {
      get() {
        // 过滤出被选中的数组，判断其长度是否与整个数组长度相等

        // return this.list.filter(e => e.chosen).length == this.list.length 如果全删完,0等于0,为true,所以会一直选中状态 因此要判断它的长度是否存在
        // 正确写法
        return (
          this.list.length &&
          this.list.filter(e => e.chosen).length == this.list.length
        );
      },
      set(newVal) {
        this.list = this.list.map(e => {
          e.chosen = newVal;
          return e;
        });
      }
    },

    ...mapState(["userInfo"])
  },
  methods: {
    touchstart() {
      this.whether = true;
    },
    touchend() {
      this.whether = false;
    },
    toList() {
      this.$router.push("/goodslist");
    },
    toOrder() {
      const goodsList = this.list.filter(e => e.chosen);
      if (goodsList.length) {
        this.$axios
          .post("/user2/order", {
            goodsList
          })
          .then(res => {
            const { code, msg } = res.data;
            if (code == 1) {
              this.chosenItems = this.list.filter(e => e.chosen);
              if (this.chooseItem.length) {
                const ids = this.chosenItems.map(e => e._id);
                this.$axios
                  .post("/user2/car/del", {
                    ids
                  })
                  .then(res => {
                    const { code, msg } = res.data;
                    if (code == 1) {
                      const list = this.list;
                      for (let i = 0; i < list.length; i++)
                        if (list[i].chosen) {
                          list.splice(i, 1);
                          i--;
                          this.getTotal();
                        }
                    } else {
                      Toast(msg);
                    }
                  });
              }
            }
            this.$router.push("/goodsorder");
            console.log(msg);
          });
      } else {
        Toast("请选择商品");
      }
    },
    // 请求购物车列表
    reqCarlist() {
      return this.$axios.post("/user2/car/list").then(res => {
        const { code, msg, list } = res.data;

        if (code == 1) {
          this.list = list;
        } else {
          // Toast(msg);
        }
      });
    },
    updateCart(item, index) {
      return this.$axios
        .post("/user2/car/update", {
          id: item._id,
          goodsNum: item.goodsNum
        })
        .then(res => {
          const { code, msg } = res.data;

          if (code == 1) {
            this.list.splice(index, 1, item);
            this.getTotal();
          } else {
            alert(msg);
          }
        });
    },

    chooseAll() {
      this.isAll = !this.isAll;
      this.getTotal();
    },
    increase(item, index) {
      item.goodsNum++;
      this.getTotal();
      this.updateCart(item, index);
      // this.list.splice(index, 1, item);
    },
    decrease(item, index) {
      if (item.goodsNum > 1) {
        item.goodsNum--;
        this.getTotal();
        this.updateCart(item, index);
      } else {
        Toast("受不了了,宝贝不能再减少了哦");
      }
      //或者用数组变异的方法
      //  item.goodsNum--;
      //  this.list.splice(index, 1, item);
    },
    getTotal() {
      let total = 0;
      for (let item of this.list) {
        if (item.chosen == true) {
          total += item.goodsNum * item.goodsPrice;
        }
      }
      this.goodsTotal = total;
    },
    chooseItem(item, index) {
      // 改变选中状态
      item.chosen = !item.chosen;

      // 数组变异
      // 删除1个下标 加入1个元素
      // 可以实现,单选效果
      // this.list[idx] = item
      this.list.splice(index, 1, item);
      this.getTotal();
    },
    delItem(item, index) {
      // 通过Id进行删除
      this.$axios
        .post("/user2/car/del", {
          ids: [item._id]
        })
        .then(res => {
          const { code, msg } = res.data;
          if (code == 1) {
            // 如果删除接口请求成功，就在页面上把这个商品给手动删掉
            this.list.splice(index, 1); // 删除下标
            this.getTotal();
          } else {
            Toast(msg);
          }
        });
    },

    // 批量删除
    delAll() {
      // 获取被选中的购物车列表
      const chosenItems = this.list.filter(e => e.chosen);

      // 如果一个都没选中，就不会往下走
      if (chosenItems.length) {
        // 导出每个购物车id
        const ids = chosenItems.map(e => e._id);

        this.$axios
          .post("/user2/car/del", {
            ids
          })
          .then(res => {
            const { code, msg } = res.data;

            if (code == 1) {
              // 在list中找有哪些被选中了,选中就删除
              const list = this.list;
              for (let i = 0; i < list.length; i++) {
                if (list[i].chosen) {
                  list.splice(i, 1);
                  i--; // 每减一个 下标自动降一个
                  this.getTotal();
                }
              }
            } else {
              Toast(msg);
            }
          });
      } else {
        Toast("请选择商品");
      }
    },
    //页面一来的全选状态
    getDetail() {
      // 价格:
      for (let item of this.list) {
        // item["chosen"] = true;
        //  this. isAll = true
        item["chosen"] = false;
      }
      this.getTotal();
    }
  },
  created() {
    // 一来到页面,接收请求
    this.reqCarlist().then(() => {
      this.getDetail();
    });
  }
};
</script>

<style lang="scss" scoped>
.car {
  .content {
    width: 100%;
    height: 200px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    color: #000;
    letter-spacing: 2px;

    h2 {
      padding: 0 0.5rem;
      font-size: 0.6rem;
    }
    p {
      padding: 0 0.5rem;
      font-size: 0.24rem;
      line-height: 0.6rem;
    }
    .btn {
      margin-left: 0.2rem;
      margin-top: 0.2rem;
      background-color: #000;
      color: aliceblue;
      font-size: 0.24rem;
      line-height: 0.7rem;
      padding: 0 0.3rem;
      width: 6.4rem;
      font-weight: bold;
      letter-spacing: 3px;
      p {
        float: right;
        font-size: 0.6rem;
      }
    }
    .touchColor {
      color: #fff;
      background-color: #7f7f7f;
    }
  }
}

.shopcar {
  .car-head {
    background-color: #000;
    display: flex;
    justify-content: space-between;
    color: #fff;
    padding: 0.2rem;
    font-size: 0.24rem;
    position: sticky;
    top: 0;
    z-index: 999;
    .ic {
      padding-right: 0.1rem;
      font-size: 0.36rem;
      vertical-align: middle;
    }
    .ic2 {
      padding-right: 0.1rem;
      font-size: 0.39rem;
    }
    .ic-copy {
      padding-left: 0.2rem;
      font-size: 0.34rem;
    }
  }

  .car-content {
    margin-bottom: 3rem;
    padding: 0.3rem 0;
    .title {
      padding-left: 0.6rem;
      line-height: 0.3rem;
      font-size: 0.26rem;
      letter-spacing: 1px;
    }

    .pic,
    .info {
      display: inline-block;
      img {
        vertical-align: middle;
        width: 2.4rem;
        height: 2.4rem;
      }
    }
    .carlist {
      padding: 0 0.2rem;
      margin: 0.3rem 0;
      position: relative;

      .choose-one {
        position: absolute;
        top: 0.16rem;
        left: 0.05rem;
        height: 0.4rem;
        background-color: #fff;
        .icon {
          font-size: 0.4rem;
        }
      }
    }

    .list-content {
      border: 1px solid #eee;
      border-radius: 1px;
    }
    .info {
      border-radius: 2px;
      width: 58%;
      padding: 0.3rem 0 0.2rem 0.3rem;
      p {
        color: #000;
        font-size: 0.3rem;
        line-height: 0.6rem;
        &:nth-child(2) {
          font-size: 0.3rem;
          font-weight: bold;
        }
      }
      .num {
        float: right;
        margin-top: 0.3rem;
        font-size: 0.3rem;
        padding-right: 0.6rem;
        span {
          text-align: center;
          display: inline-block;
          width: 0.6rem;
        }
        :nth-child(1),
        :nth-child(3) {
          border-radius: 2px;
          border: 1px solid #dcd6d6;
        }
        :nth-child(2) {
          width: 0.8rem;
          border: 1px solid #eee;
        }
        :nth-child(4) {
          color: #000;
          padding-left: 0.12rem;
        }
      }
    }
  }

  .pay-border {
    width: 100%;
    background-color: #fff;
    padding: 0.2rem;
    border-top: 1px solid #dcd6d6;
    position: fixed;
    bottom: 0.8rem;

    .pay {
      width: 7.1rem;
      height: 0.8rem;
      color: #fff;
      line-height: 0.8rem;
      background: #000;

      p {
        padding: 0 0.3rem;
        letter-spacing: 3px;
        font-weight: bold;
        font-size: 0.24rem;
        span {
          float: right;
          .row {
            font-weight: normal;
            color: #fff;
            font-size: 0.7rem;
            line-height: 0.6rem;
          }
        }
      }
    }
  }
  .touchColor {
    color: #fff;
    background-color: #7f7f7f;
  }
}
</style>