<template>
  <div>
    <!-- 轮播图 -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <!-- 通过v-for渲染图片 -->
        <template v-for="(item,index) of url">
          <div class="swiper-slide" :key="index" @click="toList">
            <img :src="$commonUrl+item">
          </div>
        </template>
      </div>
      <div class="swiper-pagination"></div>
      <!-- <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div> -->
    </div>
  </div>
</template>

<script>

//这里设置组件需要传入的数据变量
export default {
    name:'swiper',
  data() {
    return {
      url: ""
    }
  },methods: {
    toList(){
      this.$router.push('/goodslist')
    }
  },
  mounted() {
    // 使用axios获取图片资源
  this.$axios
      .post('/goods/swiper'
      )
      .then(res => {
        const url  = res.data.url
        console.log(url)

        // 把获取的图片添加到data中
        // 这个赋值操作会改变数据和视图
        this.url = url

        // 使用Vue.nextTick或者this.$nextTick，确保数据和视图全部更新完了
        // Vue.nextTick(() => {
        this.$nextTick(() => { 
          // 页面结构确定之后，再使用Swiper插件   
          new Swiper(".swiper-container", {
            // effect:'flip',
            loop: true, // 切换连贯，最后到第一张图时候
            autoplay:{
              delay:1000,
              //disableOnInteraction: true, //触碰后,自动切换停止,用于用户是否打断轮播
            },
            pagination: {
              //分页器
              el: ".swiper-pagination"
            },

            navigation: {
              //左右箭头控制
              // nextEl: ".swiper-button-next",
              // prevEl: ".swiper-button-prev"
            }
          })
        })
      })
  }
}
</script>
<style lang="scss" scoped>
    .swiper-container{
        width: 7.5rem;
        height: 3rem;

        .swiper-slide img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }            
    }
</style>
 