// 入口文件
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// 引入axios
import axios from 'axios'
// 将axios添加到Vue上,然后在组件里面使用它的时候,只需要this.$axios
Vue.prototype.$axios = axios

// 引入mint-ui
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(MintUI)

// 使用mint-ui的组件 
import {
  Loadmore,
  Indicator
} from 'mint-ui';
Vue.component(Loadmore.name, Loadmore);

// 引入vant组件
import Vant from 'vant';
import 'vant/lib/index.css'
Vue.use(Vant);

Vue.config.productionTip = false

// 配置公用的请求域 判断当前环境,如果是开发环境加个共同域名 localhost ,否则就为空
const commonUrl = process.env.NODE_ENV == 'development' ?
  'http://localhost' : ''

  Vue.prototype.$commonUrl = commonUrl

// 配置axios请求拦截器
axios.interceptors.request.use(function (config) {

  // 发送请求之前，显示loading
  Indicator.open('Loading')

  // 给页面上所有的axios请求，添加共同的请求域
  config.url = commonUrl + config.url

  // 如果没有请求参数，就给它设置一个默认值，为空对象
  config.data = config.data || {}

  // 再添加一个请求参数，为token
  config.data.token = store.state.token

  return config
}, (error) => {
  return Promise.reject(error)
});
// 配置axios的响应拦截器
axios.interceptors.response.use(function (response) {

  // 接收到响应后，关闭loading
  Indicator.close()

  // 对响应数据做点什么
  return response;
}, function (error) {
  // 对响应错误做点什么
  return Promise.reject(error);
});



new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')