import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import axios from 'axios'

/*
	一般情况下,只需要使用state和mutation就可以完成一个小项目的状态管理
	如果在改变参数的时候需要进行异步操作,就会使用action
*/
export default new Vuex.Store({
  // 定义每个参数的初始值
  state: {
    token: '',
    userInfo: null // 用户信息
  },
  // 定义改变这些参数
  mutations: {
    token(state, data) {
      state.token = data
    },
    userInfo(state, data) {
      state.userInfo = data
    }
  },

  // 分发上面这些状态变更函数,第一个参数是store的上下文对象,可以解构出它的state和commit
  actions: {
    token({
      commit
    }, data) {
      // action提交的是mutation
      commit('token', data) //this.$route.commit('token',token)
    },
    myAction({
      state,
      commit
    }, data) {
      // 这是一个异步操作，用来添加到购物车，需要传入token、商品id和选中商品的数量
      return axios.post('/user2/car/add', {
        token: state.token,
        goodsId: data.goodsId,
        goodsNum: data.goodsNum,
        goodsName: data.goodsName,
        goodsPrice: data.goodsPrice,
        goodsImg:data.goodsImg
      })
    }
  }
})


