<template>
  <div class="ic-back">
    <i class="iconfont" @click="tohome">&#xe637;</i>
    <h1>木</h1>
    <div class="register">
      <p class="content">
        <label for="username">用户名:</label>
        <input
          type="text"
          v-model="username"
          @blur="checkUsername"
          @change="checkUsername"
          @input="checkUsername"
          class="username"
          placeholder="6-10位数字、字母、下划线或$表示"
        />
        <br />
      </p>
      <p class="content-copy">
        <label for="password">密&nbsp;&nbsp;&nbsp;码:</label>
        <input
          type="password"
          class="userpwd"
          v-model="userpwd"
          @blur="checkUserpwd"
          @change="checkUserpwd"
          @input="checkUserpwd"
          placeholder="6-12位数字或字母组成"
        />
        <br />
      </p>
      <p class="tip">{{tip}}</p>
      <p class="confirm" @click="agree">同意协议并注册</p>
    </div>
    <div class="policy">
      <p>
        注册代表你已同意
        <span>用户协议</span>与
        <span>隐私政策</span>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      userpwd: "",
      tip: "",
      usernameFlag: false,
      userpwdFlag: false
    };
  },
  methods: {
    tohome() {
      this.$router.push("/home");
    },
    checkUsername() {
      this.usernameFlag = false;
      const usernameReg = /^[a-z0-9_$][a-z0-9_$]{5,10}$/gi;
      if (usernameReg.test(this.username)) {
        this.usernameFlag = true;
        this.tip = "";
      } else {
        this.tip = "用户名格式不正确";
      }
    },
    checkUserpwd() {
      this.userpwdFlag = false;
      const userpwdReg = /^[a-z0-9]{6,12}$/gi;
      if (userpwdReg.test(this.userpwd)) {
        this.userpwdFlag = true;
        this.tip = "";
      } else {
        this.tip = "密码格式不正确";
      }
    },
    agree() {
      if (this.usernameFlag == false) {
        this.tip = "用户名不能为空";
      }
      if (this.userpwdFlag == false) {
        this.tip = "密码不能为空";
      }
      if (this.usernameFlag == true && this.userpwdFlag == true) {
        this.$axios.post("/user/register", {
            username: this.username,
            password: this.userpwd
          })
          .then(res => {
            let { code, msg } = res.data;
            if (code == -1) {
              this.tip = msg;
            } else if (code == 1) {
              this.$router.push("/login");
            } else {
              alert(msg);
            }
          });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.ic-back {
  padding: 0.3rem;
  .iconfont {
    font-size: 0.4rem;
    color: #b1b0b4;
  }

  h1 {
    padding-left: 0.3rem;
    font-size: 1.2rem;
    font-weight: 200;
    color: #000;
  }

  .register {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    width: 340px;
    padding: 10px 50px 50px 50px;
    height: 450px;
    text-align: left;
    label {
      font-size: 0.3rem;
      color: #000;
    }

    .content {
      margin-top: 2rem;
      margin-bottom: 0.4rem;
    }

    .content-copy {
      margin-top: 1rem;
    }
    .username,
    .userpwd {
      width: 4rem;
      border: 0;
      height: 0.4rem;
      line-height: 0.4rem;
      padding-left: 0.2rem;
      font-size: 0.36rem;
      font-weight: bold;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }

    input::-webkit-input-placeholder {
      padding-left: 0.34rem;
      color: #aab2bd;
      font-size: 0.12rem;
      letter-spacing: 2px;
      font-weight: normal;
    }

    .tip {
      width: 5.7rem;
      text-align: center;
      color: #ff0074;
      height: 0.68rem;
      line-height: 0.6rem;
    }

    .confirm {
      display: inline-block;
      width: 5.6rem;
      line-height: 0.85rem;
      text-align: center;
      border-radius: 2px;
      font-size: 0.26rem;
      height: 0.85rem;
      background-color: #000;
      color: #fff;
      font-weight: bold;
    }
  }
  .policy {
    width: 100%;
    position: absolute;
    bottom: 0.2rem;
    left: 0;
    font-size: 0.24rem;
    p {
      text-align: center;
      color: #b2b2b9;
      font-weight: 300;
      z-index: 111;
      span {
        color: #000;
        font-weight: 400;
      }
    }
  }
}
</style>