<template>
  <div class="order">
    <h2>
      <i class="iconfont ic" @click="toCar">&#xe637;</i>确认订单
    </h2>
    <div class="address">
      <p>
        <span>
          <i class="iconfont">&#xe657;</i>
        </span>
        <span>添加收货地址</span>
        <span>
          <i class="iconfont">&#xe64c;</i>
        </span>
      </p>
    </div>
    <!-- <ul class="list">
      <li class="list-content">
        <div class="pic">
          <img />
        </div>
        <div class="info">
          <p>名称</p>
          <p>
            <span>数量*1</span>
            <span>3099</span>
          </p>
        </div>
      </li>
    </ul>-->
    <template v-for="(item,index) in list">
      <ul class="list" :key="index">
        <li class="list-content">
          <div class="pic">
            <img :src="$commonUrl+'/static'+item.goodsImg" />
          </div>
          <div class="info">
            <p>{{item.goodsName}}</p>
            <p>
              <span>{{'￥'+item.goodsPrice}}</span>
              <span>数量*{{item.goodsNum}}</span>
            </p>
          </div>
        </li>
      </ul>
    </template>
    <div class="sub" @click="sub">{{'￥'+total*1}}<span>提交订单</span></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Toast } from "vant";
export default {
  data() {
    return {
      list: [],
      total:""
    };
  },
  computed: mapState(["userInfo"]),
  methods: {
    toCar() {
      this.$router.push("/shopcar");
    },
    reqOrder() {
      return this.$axios.post("/user2/order/list").then(res => {
        const { code, msg, list } = res.data;
        if (code == 1) {
          // console.log(123);
          this.list = list;
        } else {
          Toast(msg);
        }
      });
    },
    getTotal() {
      let sum = 0;
      for (let item of this.list) {
        sum += item.goodsPrice * item.goodsNum;
      }
      this.total = sum ;
    },
    sub() {
      // 导出每个购物车id
             
      this.$axios.post("/user2/order/del", {}).then(res => {
        const { code, msg } = res.data;
        if (code == 1) {
          Toast('您的宝贝正在路上哦~');
           this.total = '';
            this.reqOrder();
            
        } else {
          Toast(msg);
        }
      });
    }
  },
  created() {
    this.reqOrder().then(()=>{
        this.getTotal();
    });
  }
};
</script>

<style lang="scss" scoped>
.order {
  margin-bottom: 1rem;
  font-weight: 400;
  font-size: 0.3rem;
  height: 100vh;
  background-color: #f5f5f7;
  color: #000;
  h2 {
    position: relative;
    background-color: #fff;
    font-size: 0.34rem;
    padding: 0.3rem 0;
    text-align: center;
    font-weight: 560;
        background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 999;
    .ic {
      font-size: 0.5rem;
      position: absolute;
      top: 0.3rem;
      left: 0.3rem;
    }
  }

  .address {
    padding: 0.3rem;
    background-color: #fff;
    border-bottom: 0.3333px solid #eee;

    span:nth-child(1) {
      padding-right: 0.3rem;
    }

    span:nth-child(3) {
      color: #3e3e3e;
      position: absolute;
      right: 0.3rem;
      i {
        font-size: 0.4rem;
      }
    }
  }

  .list {
    margin-top: 0.2rem;
    background-color: #fff;
    .list-content {
      padding: 0.3rem;
      .pic,
      .info {
        display: inline-block;
        vertical-align: top;
        height: 2.5rem;
        img {
          width: 2.4rem;
          height: 100%;
        }
      }

      .info {
        padding: 0 0 0 0.3rem;
        width: 56%;
        p {
          color: #000;
          font-size: 0.3rem;
          line-height: 0.9rem;
          &:nth-child(2) {
            margin-top: 0.3rem;
            span:nth-child(1){
              margin-right: .3rem;
              font-weight: bold;
              font-size: .34rem;
              letter-spacing: .5px;
            }
            span:nth-child(2){
              font-size: .24rem;
            }
          }
        }
      }
    }
  }

  .sub {
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-color: #000;
    color: #f5f5f7;
    line-height: 0.9rem;
    span{
      padding-left:.3rem;
    }
  }
}
</style>